@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;500;700&family=Monoton&display=swap');

html {
  --nanum: 'Nanum Gothic', sans-serif;
  --nanum-normal: 300;
  --nanum-semi-bold: 700;
  --nanum-bold: 800;
  --barlow: 'Barlow Condensed', sans-serif;
  --barlow-normal: 300;
  --barlow-semi-bold: 500;
  --barlow-bold: 700;
  --monoton: 'Monoton', cursive;
  --monoton-normal: 400;
  --black: #000000;
  --black-100: #f5f5f5;
  --black-200: #e0e0e0;
  --black-300: #bdbdbd;
  --black-400: #9e9e9e;
  --black-500: #757575;
  --black-600: #616161;
  --black-700: #424242;
  --black-800: #212121;
  --error: #d0393e;
  --cobalt: #299bff;
  --sky-blue: #7fdbff;
  --shadow: #00000020;
  --center: 0 auto;
  --backgroundColor: #1e1c1c;
  background-color: var(--backgroundColor);
  overflow-x: hidden;
}

.hidden {
  position: absolute;
  left: 9999%;
  bottom: 9999%;
}

button {
  color: var(--black-100);
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 0.5rem 0.7rem;
  font-family: var(--nanum);
  font-weight: var(--nanum-semi-bold);
}

.row {
  display: flex;
  flex-direction: row;
}

.col {
  display: flex;
  flex-direction: column;
}

.center {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
svg,
path {
  cursor: pointer;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background-color: var(--black-700) !important;
  padding: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: var(--black-100);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--black-400);
}

a {
  text-decoration: none;
}
